<template>
  <div class="container" ref="container" @scroll="remember($event)">
    <van-nav-bar fixed title="文章详情" right-text="分享" left-arrow @click-left="$router.back()" @click-right="share"/>
    <div class="detail">
      <h3 class="title">{{ article.title }}</h3>
      <div class="author">
        <van-image round width="1rem" height="1rem" fit="fill" :src="article.aut_photo" />
        <div class="text">
          <p class="name">{{ article.aut_name }}</p>
          <p class="time">{{ article.pubdate | relTime }}</p>
        </div>
        <van-button round size="small" type="info" @click="followed">{{ article.is_followed ? '已关注' : '+ 关注' }}</van-button>
      </div>
      <div class="content"><p v-html="article.content"></p></div>
      <div class="zan">
        <van-button round size="small" :class="{ active: article.attitude === 1 }" plain icon="like-o" @click="updateStatus(1)">点赞</van-button>
        &nbsp;&nbsp;&nbsp;&nbsp;
        <van-button round size="small" plain icon="delete" :class="{ active: article.attitude === 0 }" @click="updateStatus(0)">不喜欢</van-button>
      </div>
      <!-- 评论 -->
      <comment></comment>
    </div>
  </div>
</template>

<script>
import Comment from '@/views/home/components/Comment'
import { getArticleDetail, disLikes, unDisLikes, likings, unLikings } from '@/api/articles'
import { followed, unFollowed } from '@/api/user'

export default {
  name: 'article-index',
  components: { Comment },
  data () {
    return {
      article: {
        // 防止第一次获取ID报错下面activated()里面的判断
        // (因为需要根据文章id判断有没有读过这篇文章，
        // 如果第一次进来没有还拿art_id的话，
        // 没有定义就是undefined，而对undefined进行.toString()会报错)
        art_id: '',
        is_followed: false
      },
      scrollTop: 0
    }
  },
  // created () {
  //   document.addEventListener('plusready', '', false)
  // },
  activated () {
    if (this.$route.params.id !== this.article.art_id.toString()) {
      this.getArticle()
      // 重置 记忆的位置
      this.srcollTop = 0
    } else {
      this.$refs['container'].scrollTop = this.scrollTop
    }
  },
  methods: {
    share () {
      // 通过系统组件分享
      plus.share.sendWithSystem(
        {
          content: '分享调试',
          href: 'http://www.dcloud.io/'
        },
        function () {
          console.log('分享成功')
        },
        function (e) {
          console.log('分享失败：' + JSON.stringify(e))
        }
      )
    },
    // 获取文章详情
    async getArticle () {
      const data = await getArticleDetail(this.$route.params.id)
      this.article = data
    },
    remember (e) {
      this.scrollTop = e.target.scrollTop
      // console.log(this.scrollTop)
    },
    async followed () {
      try {
        // console.log(this.article.aut_id)
        if (this.article.is_followed) {
          // 取消关注
          await unFollowed(this.article.aut_id)
          this.$toast.success('取消关注成功')
          this.article.is_followed = false
          // console.log('2')
        } else {
          // console.log('1')
          // 关注
          await followed(this.article.aut_id)
          this.$toast.success('添加关注成功')
          this.article.is_followed = true
        }
      } catch (error) {
        if (error.response.status !== 401) {
          this.$toast.fail('操作失败')
        }
      }
    },
    // 点赞 取消点赞 不喜欢 取消不喜欢
    async updateStatus (btnType) {
      try {
        if (btnType === 1) {
          // 点赞 取消点赞
          if (this.article.attitude === 1) {
            this.article.attitude = -1
            await unLikings(this.article.art_id)
          } else {
            this.article.attitude = 1
            await likings(this.article.art_id)
          }
        } else {
          // 不喜欢 取消不喜欢
          if (this.article.attitude === 0) {
            this.article.attitude = -1
            await unDisLikes(this.article.art_id)
          } else {
            this.article.attitude = 0
            await disLikes(this.article.art_id)
          }
        }
        this.$toast.success('操作成功')
      } catch (error) {
        if (error.response.status !== 401) {
          this.$toast.fail('操作失败')
        }
      }
    }
  }
}
</script>

<style lang="less" scoped>
.container {
  height: 100%;
  overflow-y: auto;
  box-sizing: border-box;
}
.detail {
  padding: 46px 10px 44px;
  // height: 1000%;
  .title {
    font-size: 18px;
    line-height: 2;
  }
  .zan {
    text-align: center;
    padding: 10px 0;
    .active {
      border-color: red;
      color: red;
    }
  }
  .author {
    padding: 10px 0;
    display: flex;
    .text {
      flex: 1;
      padding-left: 10px;
      line-height: 1.5;
      .name {
        font-size: 14px;
        margin: 0;
      }
      .time {
        margin: 0;
        font-size: 12px;
        color: #999;
      }
    }
  }
  .content {
    padding: 20px 0;
    overflow: hidden;
    white-space: pre-wrap;
    word-break: break-all;
    /deep/ img {
      max-width: 100%;
      background: #f9f9f9;
    }
    /deep/ code {
      white-space: pre-wrap;
    }
  }
}
</style>
