<template>
  <div class="comment">
    <!-- 评论列表 -->
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      :immediate-check="false"
      @load="getComments"
    >
      <div
        class="item van-hairline--bottom van-hairline--top"
        v-for="(item,i) in comments"
        :key="i"
      >
        <van-image round width="1rem" height="1rem" fit="fill" :src="item.aut_photo" />
        <div class="info">
          <p>
            <span class="name">{{item.aut_name}}</span>
            <span style="float:right">
              <span class="van-icon van-icon-good-job-o zan"></span>
              <span class="count">{{item.like_count}}</span>
            </span>
          </p>
          <p>{{item.content}}</p>
          <p>
            <span class="time">{{item.pubdate|relTime}}</span>&nbsp;
            <van-tag plain @click="openReplyDailog(item.com_id.toString())">{{item.reply_count}} 回复</van-tag>
          </p>
        </div>
      </div>
    </van-list>
    <!-- 回复 -->
    <van-action-sheet
      @close="reply.com_id=null"
      v-model="showReply"
      class="reply_dailog"
      title="回复评论"
    >
      <van-list
        v-model="reply.loading"
        @load="getReplay"
        :immediate-check="false"
        :finished="reply.finished"
        finished-text="没有更多回复了"
      >
        <div
          class="item van-hairline--bottom van-hairline--top"
          v-for="(item,i) in reply.list"
          :key="i"
        >
          <van-image
            round
            width="1rem"
            height="1rem"
            fit="fill"
            src="https://img.yzcdn.cn/vant/cat.jpeg"
          />
          <div class="info">
            <p>
              <span class="name">{{item.aut_name}}</span>
            </p>
            <p>{{item.content}}</p>
            <p>
              <span class="time">{{item.pubdate|relTime}}</span>
            </p>
          </div>
        </div>
      </van-list>
    </van-action-sheet>
    <!-- 底部输入框 -->
    <div class="reply-container van-hairline--top">
      <van-field v-model.trim="value" placeholder="写评论...">
        <van-loading v-if="submiting" slot="button" type="spinner" size="16px"></van-loading>
        <span class="submit" v-else slot="button" @click="submit">提交</span>
      </van-field>
    </div>
  </div>
</template>

<script>
import { getCommentsOrReplys, commentOrReply } from '@/api/articles'
export default {
  data () {
    return {
      // 输入的内容
      value: '',
      submiting: false,
      // 评论框相关数据
      loading: false,
      finished: false,
      offset: null,
      comments: [],
      // 回复框相关数据
      showReply: false,
      reply: {
        loading: false,
        finished: false,
        offset: null,
        com_id: '',
        list: []
      }
    }
  },
  // 当前组件在Article组件使用，这个组件被缓存了。
  // 目的：每次进入文章的时候，拿到最新的评论数据。
  // 什么时候来加载评论数据？ activated钩子加载第一次数据
  // van-list 在初始化的时候主动触发一次 @load 事件
  // 为了避免van-list主动触发的@load事件与activated里面重复取了数据，需要将
  // van-list主动触发的@load事件的功能关闭，
  // 用immediate-check是否在初始化时立即执行滚动位置检查
  activated () {
    // 重置数据
    this.finished = false
    this.loading = true
    this.comments = []
    this.getComments()
  },
  methods: {
    // type (是) (a或c)评论类型，a-对文章(article)的评论，c-对评论(comment)的回复
    // source(是)源id，文章id或评论id
    // offset(否)获取评论数据的偏移量，值为评论id，表示从此id的数据向后取，不传表示从第一页开始读取数据
    // limit(否)获取的评论数据个数，不传表示采用后端服务设定的默认每页数据量
    async getComments () {
      const data = await getCommentsOrReplys({
        type: 'a',
        source: this.$route.params.id,
        offset: this.offset
      })
      this.comments.push(...data.results)
      this.loading = false
      if (data.last_id > data.end_id) {
        // 加载下一页数据  需要上一次加载数据最后的ID
        this.offset = data.last_id
      } else {
        this.finished = true
      }
    },
    async openReplyDailog (id) {
      this.reply = {
        loading: true,
        finished: false,
        offset: null,
        list: []
      }
      // 打开对话框
      this.showReply = true
      // 获取对应评论id
      this.reply.com_id = id
      this.getReplay()
    },
    async getReplay () {
      const data = await getCommentsOrReplys({
        type: 'c',
        source: this.reply.com_id,
        offset: this.reply.offset
      })
      this.reply.list.push(...data.results)
      this.reply.loading = false
      if (data.last_id > data.end_id) {
        this.reply.offset = data.last_id
      } else {
        this.reply.finished = true
      }
    },
    async submit () {
      try {
        if (!this.value) return false
        // 开启提交效果
        this.submiting = true
        if (this.reply.com_id) {
          // 添加回复
          const data = await commentOrReply(
            this.reply.com_id,
            this.value,
            this.$route.params.id
          )
          this.$toast.success('添加回复成功')
          console.log(data)

          this.reply.list.unshift(data.new_obj)
          // 给当前评论的回复数+1
          this.comments.find(
            item => item.com_id.toString() === this.reply.com_id
          ).reply_count++
        } else {
          // 添加评论
          const data = await commentOrReply(this.$route.params.id, this.value)
          this.$toast.success('添加评论成功')
          console.log(data)
          this.comments.unshift(data.new_obj)
        }
        // 结束提交中效果
        this.submiting = false
        this.value = ''
      } catch (error) {
        this.$toast.fail('操作失败')
        // 结束提交中效果
        this.submiting = false
      }
    }
  }
}
</script>

<style lang="less" scoped>
.van-popup--bottom {
  &.van-popup--round {
    border-radius: 0;
  }
}
.reply_dailog {
  height: 100%;
  max-height: 100%;
  display: flex;
  overflow: hidden;
  flex-direction: column;
  .van-action-sheet__header {
    background: #3296fa;
    color: #fff;
    .van-icon-close {
      color: #fff;
    }
  }
  .van-action-sheet__content {
    flex: 1;
    overflow-y: auto;
    padding: 0 10px 44px;
  }
}
.comment {
  margin-top: 10px;
  /deep/ .item {
    display: flex;
    padding: 10px 0;
    .info {
      flex: 1;
      padding-left: 10px;
      .name {
        color: #069;
      }
      .zan {
        vertical-align: middle;
        padding-right: 2px;
      }
      .count {
        vertical-align: middle;
        font-size: 10px;
        color: #666;
      }
      .time {
        color: #666;
      }
      p {
        padding: 5px 0;
        margin: 0;
      }
    }
  }
  /deep/ .van-button:active::before {
    background: transparent;
  }
}
.reply-container {
  position: fixed;
  left: 0;
  bottom: 0;
  height: 44px;
  width: 100%;
  background: #f5f5f5;
  z-index: 9999;
  .submit {
    font-size: 12px;
    color: #3296fa;
  }
}
</style>
